'use client';

import * as React from 'react';
import { classNames } from '@vkontakte/vkjs';
import { ImageBase, type ImageBaseBadgeProps, ImageBaseContext } from '../../ImageBase/ImageBase';
import styles from './AvatarBadge.module.css';

export type AvatarBadgeProps = ImageBaseBadgeProps;

export const AvatarBadge: React.FC<AvatarBadgeProps> = ({
  className,
  ...restProps
}: AvatarBadgeProps) => {
  const { size } = React.useContext(ImageBaseContext);
  return (
    <ImageBase.Badge
      {...restProps}
      className={classNames(styles.host, size < 96 && styles.shifted, className)}
    />
  );
};

AvatarBadge.displayName = 'Avatar.Badge';
